import React from 'react';
import './DataTable.scss';
import Checkbox from './Checkbox/Checkbox';
import ColumnHeader from './ColumnHeader/ColumnHeader';


export default function DataTable(props) {
  const [sortBy, setSortBy] = React.useState("Date");
  return (
    <React.Fragment>
      <div className="data-table__spacer" />
      <table className="data-table">
        <thead>
          <tr>
            <th style={{ width: '2.7%' }}><Checkbox checked="checked" /></th>
            <th><ColumnHeader title="Date" sortable={sortBy} /></th>
            <th><ColumnHeader title="Payee" sortable={sortBy} /></th>
            <th><ColumnHeader title="Description" sortable={sortBy} /></th>
            <th><ColumnHeader title="Type" sortable={sortBy} /></th>
            <th><ColumnHeader title="Selection" sortable={sortBy} /></th>
            <th><ColumnHeader title="Reference" sortable={sortBy} /></th>
            <th><ColumnHeader title="VAT" sortable={sortBy} /></th>
            <th><ColumnHeader title="Spent" sortable={sortBy} alignment="right" /></th>
            <th><ColumnHeader title="Received" sortable={sortBy} alignment="right"/></th>
            <th><ColumnHeader title="REC?" sortable={sortBy} /></th>
            <th><ColumnHeader title="Action" sortable={sortBy} alignment="center"/></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>Checkbox</td>
            <td>date</td>
            <td>Payee</td>
            <td>Description</td>
            <td>Type</td>
            <td>Selection</td>
            <td>Reference</td>
            <td>VAT</td>
            <td>Spent</td>
            <td>Received</td>
            <td>REC?</td>
            <td>Action</td>
          </tr>
        </tbody>
      </table>
    </React.Fragment>
  );
}
